.addform {
  margin: 20px 10px;
  overflow: hidden;
}
.addform div {
  margin: 10px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  font-size: 2em;
}

.addform div input,
textarea {
  border-radius: 10px;
  border: 1px outset;
  outline-style: none;
  font-size: 1.2rem;
  padding: 8px;
  width: calc(80% - 100px);
  background-color: black;
  color: white;
}

:is(.addform div input, textarea):focus {
  border: transparent;
  box-shadow: 0px 0px 10px 1px skyblue;
}

.addform div button {
  padding: 5px 15px;
  font-size: 20px;
  background-color: black;
  border-color: white;
  color: white;
  border-radius: 10px;
  margin: 20px auto 0px;
  border-style: outset;
}
.addform div button:hover {
  background-color: white;
  color: black;
}

@media all and (max-width: 1000px) {
  .addform div {
    flex-flow: column wrap;
    align-items: center;
  }
  .addform div input,
  textarea {
    width: 100%;
    margin-top: 5px;
  }
}
